import { Injectable } from "@angular/core";
import { Observable } from 'rxjs';
import { _alertBomb } from '../pages/common/_alert'
import { StorageService } from './locationstorageService'

@Injectable()
export class AuthorityService {
    public flag;
    public roleName = "";
    constructor(
        private Storage: StorageService,
        public _alert: _alertBomb
    ) {

    }
    //1.2.3.
    validate(event) {
        console.log(event)
        return new Promise((resolve, reject) => {
            this.Storage.GetStorage("userLogin").subscribe(res => {
                res.then(res => {
                    try {
                        this.roleName = res.roleName
                        if (event == this.roleName) {
                            this.flag = true
                            resolve()
                        } else if (event == "" || this.roleName == "") {
                            this.flag = true
                            resolve()
                        } else {
                            this.Alert()
                            this.flag = false
                        }
                    } catch (e) {

                    }
                })
            })
        })
        // switch (event) {
        //     case "1":
        //         this.locroleName = ""
        //         break;
        //     case "2":
        //         this.locroleName = ""
        //         break;
        //     case "3":
        //         this.locroleName = ""
        //         break;
        // }

    }
    Alert() {
        var parpam = {
            title: "",
            subTitle: "<br/>",
            buttons: [
                {
                    text: "",
                    handler: () => {

                    }
                }
            ],
            cssClass: "outsuccse only"
        }
        var addbuton = {
            text: null
        }
        var addInput = []
        this._alert._alertSmlpe(parpam, addbuton, addInput, function (data) { })
    }
}


